import React, { Component } from 'react';
import { NavLink, Link } from 'react-router-dom';
import IconButton from 'material-ui/IconButton';
import Button from 'material-ui/Button';

const activeNav = {
  borderBottom: '2px solid rgb(28, 148, 224)',
  color: '#1b95e0'
};

class Header extends Component {
  render() {
    return (
      <div className="top-bar">
        <div className="global-nav">
          <div className="global-nav-innner">
            <div className="nav-inner-left">
              <ul className="nav-list">
                <li className="nav-list__item">
                  <div className="toggle-menu">
                    <IconButton
                      color="inherit"
                      aria-label="Menu"
                      onClick={this.props.openDrawer}
                    >
                      <i className="material-icons">menu</i>
                    </IconButton>
                  </div>
                </li>
                <li className="nav-list__item">
                  <NavLink exact to="/" activeStyle={activeNav}>
                    Home
                  </NavLink>
                </li>
                <li className="nav-list__item">
                  <NavLink to="/moments" activeStyle={activeNav}>
                    Moments
                  </NavLink>
                </li>
                <li className="nav-list__item">
                  <NavLink to="/newpost" activeStyle={activeNav}>
                    New
                  </NavLink>
                </li>
              </ul>
            </div>
            <div className="nav-inner-right">
              <div className="sign-in-up">
                <Button
                  variant="raised"
                  color="primary"
                  size="small"
                  component={props => <Link to="/signin" {...props} />}
                >
                  Sign In
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;



